  const calc_aqi = (pm, pm_min, pm_max, aqi_min, aqi_max) =>{
    return Math.round((((pm - pm_min) * (aqi_max - aqi_min)) / pm_max - pm_min ) + aqi_min); 

}

const get_aqi = (pm) => {
      if(pm > 250.5)
          return calc_aqi(pm, 251, 500, 401, 500);
      else if(pm > 120.5)
          return calc_aqi(pm, 121, 250, 301, 400);
      else if(pm > 90.5)
          return calc_aqi(pm, 91, 120, 201, 300);
      else if(pm > 60.5)
          return calc_aqi(pm, 61, 90, 101, 200);
      else if(pm > 30.5)
          return calc_aqi(pm, 31, 60, 51, 100);
      else if(pm > 0.5)
          return calc_aqi(pm, 1, 30, 1, 50);
      else
          return 0
}
const get_aqi10 = (pm) => {
  if(pm > 430.5)
      return calc_aqi(pm, 431, 500, 401, 500);
  else if(pm > 350.5)
      return calc_aqi(pm, 351, 430, 301, 400);
  else if(pm > 250.5)
      return calc_aqi(pm, 251, 350, 201, 300);
  else if(pm > 100.5)
      return calc_aqi(pm, 101, 250, 101, 200);
  else if(pm > 50.5)
      return calc_aqi(pm, 51, 100, 51, 100);
  else if(pm > 0.5)
      return calc_aqi(pm, 1, 50, 1, 50);
  else
      return 0
}
